<template>
  <div class="me clear">
     <div class="line-content clear">
        <div class="line-item" v-for="item in actor">
          <div class="line-item-left">{{item.year}}</div>
          <div class="line"></div>
          <div class="line-item-right">
            <div  v-for="cont in item.content">
            <div class="title">{{cont.title}}</div>
            <div class="content" v-html="cont.box">
            </div>
            </div>
          </div>
        </div>
     </div>
  </div>
</template>

<script>
  import pagedata from '@/dataJson/me'
export default {
  name: 'HelloWorld',
  data () {
    return {
      actor:[],
      pagemess:{
        totalpage:0,
        page:1
      },
    }
  },
  mounted:function () {
    this.getdata()
    console.log(pagedata)

  },

  methods:{
    gopage:function (page) {
      this.$router.push({path:"zhuye",query:{page:page}});
      this.getdata()
    },
    getdata:function () {
      let page = this.$route.query.page;
      this.pagemess.totalpage = pagedata.length;
      if(!page){
        this.pagemess.page = 1;
        this.actor = pagedata[0]
        console.log(this.actor);
      }else {
        if(page> pagedata.length || page<1){
          this.pagemess.page = 1;
          this.actor = pagedata[0]
          return
        }
        this.pagemess.page = parseInt(page);
        this.actor = pagedata[page-1];
      }
    }
  }
}


</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .me{
    height: auto;
  }
  .line-content{
  }
  .line-item{
    position: relative;
    padding-bottom: 20px;

  }
  .line-item:hover .line:before{
    background: #eb7350;
  }
  .line-item:hover  .line-item-right .title{
    color: #eb7350;
  }
  .line{
    position: absolute;
    border-left:solid 2px #eb7350;
    margin-left: 70px;
    height: 100%;
    padding-top: 10px;
    padding-left:1px ;

  }
  .line:before{
    content: "";
    width: 9px;
    height: 9px;
    display: inline-block;
    border: 3px solid #fff;
    border-radius: 50%;
    box-shadow: 0 1px 1px rgba(0,0,0,0.2), inset 0 1px 1px rgba(0,0,0,0.1);
    background: #ccc;
    margin-left: -9px;

  }
  .line-item-left{
    position: absolute;
    margin-left: 15px;
    margin-top: 10px;
  }
  .line-item-right{
    margin-left: 100px;
    background-color: #fff;
    border-radius: 2px;
    box-shadow: #999 0px 2px 3px;
    padding:10px 20px;
    min-height: 20px;
  }
  .line-item-right:before{
    content: "";
    border: solid 9px ;
    border-color: transparent #ffffff transparent transparent ;
    position: absolute;
    margin-left: -38px;
  }
  .line-item-right .title{
    font-size: 16px;
    color: #555;
    line-height: 30px;
    /*border-bottom: solid 1px #eee;*/
  }
  .line-item-right .title:before{
    content: "*";
    /*border-left: solid 5px #eb7350;*/
    padding-right: 10px;
    color: #eb7350;
    font-weight: 600;
  }
  .line-item-right .content{
    padding-left: 15px;
    text-indent: 26px;
    line-height: 30px;
    font-size: 13px;
  }
</style>



